import { URLInputButton, BlockControls } from "@wordpress/block-editor";

export default function Toolbar(props) {
  const { blockProps, attributes, setAttributes } = props
  const { url } = attributes;

  return <div {...blockProps}>
    <BlockControls group="block">
      <URLInputButton
        url={ url }
        onChange={ url => setAttributes( { url } ) }
      />
    </BlockControls>
  </div>
}