import { useBlockProps } from '@wordpress/block-editor';

export default function save(props) {
  const { attributes } = props;
  const { url, icon, color, tag, backgroundColor, borderRadius, tailleContour, tailleIcon, borderColor, borderSize, borderStyle, openNewTab } = attributes;
  const blockProps = useBlockProps.save();

  const Tag = tag ? tag : 'div';

  return (
    <>
      { url?.length
        ? <a
            {...blockProps} 
            href={url}
            target={openNewTab ? '_blank' : '_self'}
            style={{
              backgroundColor: backgroundColor,
              borderRadius: borderRadius + '%',
              width: tailleContour + 'rem',
              height: tailleContour + 'rem',
              borderStyle: borderStyle,
              borderWidth: borderSize + 'rem',
              borderColor: borderColor ? borderColor : 'transparent',
              '--icon-size': tailleIcon + 'rem'
            }}
          >
            <Tag width={tailleIcon * 16} height={tailleIcon * 16} className={ `icon ${icon} ${color}` }>{ icon }</Tag>
          </a>
        : (
          <div
            {...blockProps}
            style={{
              backgroundColor: backgroundColor,
              borderRadius: borderRadius + '%',
              width: tailleContour + 'rem',
              height: tailleContour + 'rem',
              borderStyle: borderStyle,
              borderWidth: borderSize + 'rem',
              borderColor: borderColor ? borderColor : 'transparent',
              '--icon-size': tailleIcon + 'rem'
            }}
          >
            <Tag width={tailleIcon * 16} height={tailleIcon * 16} className={`icon ${icon} ${color}`} >{icon}</Tag>
          </div>
        )        
      }
    </>);
}
