import { __ } from '@wordpress/i18n';
import { Button } from "@wordpress/components";
import { useState } from "@wordpress/element";
import { IconSelector } from "../iconSelector";


export default function Block( props ) {
  const { blockProps, attributes, setAttributes } = props
  const { icon, color, tag, backgroundColor, borderRadius, tailleContour, tailleIcon, borderColor, borderSize, borderStyle } = attributes
  const [ isOpen, setOpen ] = useState( false );
  const openModal = () => setOpen( true );
  const closeModal = () => setOpen(false);

  const Tag = tag ? tag : 'div';

  return (
    <>
      { isOpen ?
        <IconSelector
          isOpen={ isOpen }
          closeModal={ closeModal }
          hasColorChoice={ true }
          setIcon={ (icon, color) => {
            setAttributes({ icon, color });
          }}
        />
        : (<></>)
      }

      <div
        {...blockProps}
        style={{
          backgroundColor: backgroundColor,
          borderRadius: borderRadius + '%',
          width: tailleContour + 'rem',
          height: tailleContour + 'rem',
          borderStyle: borderStyle,
          borderWidth: borderSize + 'rem',
          borderColor: borderColor ? borderColor : 'transparent',
          '--icon-size': tailleIcon + 'rem'
        }}
      >
        <Button
          aria-haspopup="true"
          variant="secondary"
          className="is-secondary"
          title={__("S√©lectionnez une ic√¥ne", "ocade-blocks")}
          onClick={ openModal }>
          {
            (icon === "")
              ? <i
                className={`icon etandard ${color} blocks-icon-picker-default`} >etandard</i>
              : <Tag className={ `icon ${icon} ${color}` } >{ icon }</Tag>
          }
        </Button>
      </div>
    </>
  );
}
